'use client'
import React, { useState } from 'react'
import { Servicio } from '../../../entities/servicios'

const serviciosDisponibles: Servicio[] = [
  { idServicio: 1, nombre: 'Mantenimiento', costo: 150 },
  { idServicio: 2, nombre: 'Instalación', costo: 200 },
  { idServicio: 3, nombre: 'Reparación', costo: 180 }
]

export default function ServiceForm() {
  const [cliente, setCliente] = useState('')
  const [serviciosSeleccionados, setServiciosSeleccionados] = useState<Servicio[]>([])

  const agregarServicio = (servicio: Servicio) => {
    setServiciosSeleccionados([...serviciosSeleccionados, servicio])
  }

  const eliminarServicio = (id: number) => {
    setServiciosSeleccionados(serviciosSeleccionados.filter(s => s.idServicio !== id))
  }

  const total = serviciosSeleccionados.reduce((acc, s) => acc + s.costo, 0)

  const handleSubmit = () => {
    alert(`Venta registrada para ${cliente} con total: S/ ${total}`)
    setCliente('')
    setServiciosSeleccionados([])
  }

  return (
    <div className="box-white">
      <h2 className="section-title">Registrar Venta de Servicio</h2>

      <input
        type="text"
        placeholder="Nombre del cliente"
        value={cliente}
        onChange={(e) => setCliente(e.target.value)}
        className="search-input mb-3"
      />

      <div className="filter-group">
        {serviciosDisponibles.map(servicio => (
          <button
            key={servicio.idServicio}
            className="primary-btn"
            onClick={() => agregarServicio(servicio)}
          >
            {servicio.nombre} (S/ {servicio.costo})
          </button>
        ))}
      </div>

      {serviciosSeleccionados.length > 0 && (
        <div className="mt-4">
          <h4 className="mb-2">Servicios Seleccionados:</h4>
          <ul>
            {serviciosSeleccionados.map(s => (
              <li key={s.idServicio}>
                {s.nombre} - S/ {s.costo} {' '}
                <button className="action-btn delete" onClick={() => eliminarServicio(s.idServicio)}>Quitar</button>
              </li>
            ))}
          </ul>
          <p className="mt-2 font-bold">Total: S/ {total}</p>
          <button className="primary-btn mt-3" onClick={handleSubmit}>Registrar Venta</button>
        </div>
      )}
    </div>
  )
}
