'use client'
import React from 'react'
import { VentaServicio } from '../../../entities/servicios'

interface Props {
  searchTerm: string
}

const mockVentas: VentaServicio[] = [
  {
    idVentaServicio: 1001,
    idCliente: 501,
    descuento: 0,
    costoTotal: 300,
    fechaRegistro: '2025-06-10'
  },
  {
    idVentaServicio: 1002,
    idCliente: 502,
    descuento: 0,
    costoTotal: 450,
    fechaRegistro: '2025-06-11'
  }
]

export default function ServicesTable({ searchTerm }: Props) {
  const filtrados = mockVentas.filter(v =>
    v.idCliente.toString().includes(searchTerm)
  )

  return (
    <div className="products-table-container">
      <table className="products-table">
        <thead>
          <tr>
            <th>ID Venta</th>
            <th>ID Cliente</th>
            <th>Costo Total</th>
            <th>Fecha</th>
          </tr>
        </thead>
        <tbody>
          {filtrados.map(v => (
            <tr key={v.idVentaServicio}>
              <td>{v.idVentaServicio}</td>
              <td>{v.idCliente}</td>
              <td>S/ {v.costoTotal}</td>
              <td>{v.fechaRegistro}</td>
            </tr>
          ))}
          {filtrados.length === 0 && (
            <tr>
              <td colSpan={4} className="text-center">No se encontraron resultados</td>
            </tr>
          )}
        </tbody>
      </table>
    </div>
  )
}
